import React from 'react';
import './style.scss';
import { Form, Icon, Input, Button, Checkbox, Alert } from 'antd';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { companyLogin } from '../../../store/actions/userAction';
import Footer from '../../Footer';
import Header from '../../Header';
class CompanyLoginForm extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        const { login } = this.props;
        const { email, password } = values;
        await login({
          email,
          password
        });
        const { history } = this.props;
        history.push('/company/home');
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { company } = this.props;

    return (
      <React.Fragment>
        <Header />
        <div className="company-login-container">
          <div className="form-container">
            <h3 className="login-form-title"> </h3>
            <Form onSubmit={this.handleSubmit} style={{ width: '100%' }}>
              <label className="login-form-label"> </label>
              <Form.Item>
                {getFieldDecorator('email', {
                  rules: [
                    {
                      required: true,
                      message: '   '
                    }
                  ]
                })(<Input prefix={<Icon />} />)}
              </Form.Item>
              <label className="login-form-label"> </label>
              <Form.Item>
                {getFieldDecorator('password', {
                  rules: [
                    { required: true, message: '   ' }
                  ]
                })(<Input prefix={<Icon />} type="password" />)}
                <Link to="/company/resetPassword">  </Link>
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('remember', {
                  valuePropName: 'checked',
                  initialValue: true
                })(<Checkbox></Checkbox>)}
              </Form.Item>
              {/* <Form.Item className="check-user-existance"> */}
              {company.loginError && (
                <Alert
                  type="error"
                  message={
                    company.loginError.response
                      ? company.loginError.response.data
                      : '  '
                  }
                />
              )}
              <br />
              <div className="login-btn-cont">
                <Button
                  type="primary"
                  htmlType="submit"
                  className="login-form-button login-form-btn"
                >
                   
                </Button>
              </div>
              <div className="create-new-account">
                   <Link to="/company/signup">  </Link>
              </div>
              {/* </Form.Item> */}
            </Form>
          </div>
          <div style={{ width: '100%' }}>
            <Footer />
          </div>
        </div>
      </React.Fragment>
    );
  }
}
const mapStateToProps = ({ user }) => {
  return {
    company: user
  };
};

const mapDispatchToProps = dispatch => {
  return {
    login: params => dispatch(companyLogin(params))
  };
};
const CompanyLogin = Form.create({ name: 'CompanyLoginForm' })(
  CompanyLoginForm
);
export default connect(mapStateToProps, mapDispatchToProps)(CompanyLogin);
