import React from 'react';
import { Row, Col, Input, DatePicker, Radio, Modal } from 'antd';
import moment from 'moment';
import Footer from '../Footer';
import LoginNavbar from '../Header/LoginNavbar';
import TempForm from '../../services/newForm';
import kareem from '../../images/kareem.png';
import marsool from '../../images/marsool.png';
import n3na3 from '../../images/n3na3.png';
import zad from '../../images/zad.png';
import hunger from '../../images/hunger.png';

const { deliveryData } = TempForm;
class JobForm extends React.Component {
  state = {
    visible: false,
    error: false
  };

  handleChange = e => {
    const { value, name } = e.target;

    this.setState({
      [name]: value
    });
  };

  handleDateChange = birthDate => {
    // console.log('dateString', dateString, moment(birthDate));

    this.setState({
      birthDate: moment(birthDate)
    });
  };

  add = async () => {
    const {
      name,
      gender,
      street,
      city,
      mobile,
      email,
      avilableCar,
      carType,
      carModel,
      timeToDelivier,
      jobTitle,
      company,
      exp,
      ptwayMember,
      mobileOS
    } = this.state;
    if (
      (!name ||
        !gender ||
        !street ||
        !city ||
        !mobile ||
        !/^\d{10}$/.test(mobile) ||
        !email ||
        !avilableCar ||
        !carType ||
        !carModel ||
        !timeToDelivier ||
        !jobTitle,
      !company || !exp || !ptwayMember || !mobileOS)
    ) {
      this.setState({
        error: true
      });
    } else {
      await deliveryData(this.state);
      this.setState({
        visible: true
      });
    }
  };
  handleCancel = e => {
    this.setState({
      visible: false
    });
  };

  render() {
    const dateFormat = 'DD/MM/YYYY';
    const { error } = this.state;
    // console.log('here', !/^\d{10}$/.test(this.state.mobile));

    return (
      <React.Fragment>
        <LoginNavbar />
        <Row>
          <Col md={4}></Col>
          <Col md={15} className="job-new-form">
            <h2>   !</h2>
            <label> :</label>
            <Input onChange={this.handleChange} name="name" />
            {error && !this.state.name && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <div className="pers-info">
              <div>
                <h4> :</h4>
                <DatePicker
                  placeholder={dateFormat}
                  format={dateFormat}
                  className="form-date"
                  onChange={this.handleDateChange}
                />
                {error && !this.state.birthDate && (
                  <span style={{ color: 'red' }}>  </span>
                )}
              </div>
              <div>
                <h4>:</h4>
                <Radio.Group
                  onChange={this.handleChange}
                  className="radio-select"
                  name="gender"
                >
                  <Radio.Button value=""></Radio.Button>
                  <Radio.Button value=""></Radio.Button>
                </Radio.Group>
                {error && !this.state.gender && (
                  <span style={{ color: 'red' }}>  </span>
                )}
              </div>
            </div>
            <label></label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select"
              name="isSaudi"
            >
              <Radio.Button value=""></Radio.Button>
              <Radio.Button value=" "> </Radio.Button>
            </Radio.Group>
            <br />
            <br />
            <label> </label>{' '}
            <span style={{ fontSize: '14px', color: '#d0d0d0' }}>
                  
            </span>
            <br />
            <Input onChange={this.handleChange} name="nationality" />
            <div className="location-">
              <div>
                <label>:</label>
                <br />
                <Input onChange={this.handleChange} name="city" />
                {error && !this.state.city && (
                  <span style={{ color: 'red' }}>  </span>
                )}
              </div>
              <div>
                <label> :</label>
                <br />
                <Input onChange={this.handleChange} name="street" />
                {error && !this.state.street && (
                  <span style={{ color: 'red' }}>  </span>
                )}
                <br />
              </div>
            </div>
            <label> :</label>
            <Input maxlength="10" onChange={this.handleChange} name="mobile" />
            {error && !this.state.mobile && (
              <span style={{ color: 'red' }}>   </span>
            )}
            {error && !/^\d{10}$/.test(this.state.mobile) && (
              <span style={{ color: 'red' }}>
                {' '}
                      10 
              </span>
            )}
            <br />
            <label> :</label>
            <Input onChange={this.handleChange} name="email" />
            {error && !this.state.email && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <label>   </label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select"
              name="avilableCar"
            >
              <Radio.Button value=""></Radio.Button>
              <Radio.Button value=""></Radio.Button>
            </Radio.Group>
            {error && !this.state.avilableCar && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <br />
            <label> :</label>
            <br />
            <Input onChange={this.handleChange} name="carType" />
            {error && !this.state.carType && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <label> :</label>
            <br />
            <Input onChange={this.handleChange} name="carModel" />
            {error && !this.state.carModel && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <label>   :</label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select select-mob"
              name="jobTitle"
            >
              <Radio.Button value=""></Radio.Button>
              <Radio.Button value=" "> </Radio.Button>
              <Radio.Button value=""></Radio.Button>
            </Radio.Group>
            {error && !this.state.jobTitle && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <br />
            <label>    </label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select select-mob"
              name="timeToDelivier"
            >
              <Radio.Button value=" ">
                 
              </Radio.Button>
              <Radio.Button value=" ">
                 
              </Radio.Button>
              <Radio.Button value="  ">
                  
              </Radio.Button>
              <Radio.Button value=" "> </Radio.Button>
            </Radio.Group>
            {error && !this.state.timeToDelivier && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <br />
            <label>    </label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select select-mob select-image"
              name="company"
            >
              <div>
                <Radio.Button value="zad">
                  <img src={zad} alt="zad" />
                </Radio.Button>
                <Radio.Button value="hunger" alt="hunger">
                  <img src={hunger} />
                </Radio.Button>
                <Radio.Button value="kareem">
                  <img src={kareem} alt="kareem" />
                </Radio.Button>
              </div>
              <div>
                <Radio.Button value="n3na3">
                  <img src={n3na3} alt="n3na3" />
                </Radio.Button>
                <Radio.Button value="marsool">
                  <img src={marsool} alt="marsool" />
                </Radio.Button>
              </div>
            </Radio.Group>
            {error && !this.state.company && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <br />
            <label>  :</label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select"
              name="mobileOS"
            >
              <Radio.Button value="android">Android</Radio.Button>
              <Radio.Button value="ios">iOS</Radio.Button>
            </Radio.Group>
            {error && !this.state.mobileOS && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <br />
            <label>   </label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select"
              name="exp"
            >
              <Radio.Button value=""></Radio.Button>
              <Radio.Button value=""></Radio.Button>
            </Radio.Group>
            {error && !this.state.exp && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <br />
            <label>     PTway</label>
            <br />
            <br />
            <Radio.Group
              onChange={this.handleChange}
              className="radio-select"
              name="ptwayMember"
            >
              <Radio.Button value=""></Radio.Button>
              <Radio.Button value=""></Radio.Button>
            </Radio.Group>
            {error && !this.state.ptwayMember && (
              <span style={{ color: 'red' }}>  </span>
            )}
            <br />
            <br />
            <button className="job-form-btn" onClick={this.add}>
              
            </button>
          </Col>
          <Col md={4}></Col>
        </Row>
        <Footer />
        <Modal
          visible={this.state.visible}
          onCancel={this.handleCancel}
          footer={false}
        >
          <div className="success-modal">
            <h2>         .</h2>
            <br />
            <h3 style={{ color: '#049ad0' }}>
                     !
            </h3>
            <br />
            <button>
              <a href="https://www.ptway.net/user/login">
                <span className="coloor">  PTway</span>
              </a>
            </button>
          </div>
        </Modal>
      </React.Fragment>
    );
  }
}

export default JobForm;
