import React from 'react';
import { Row, Drawer, Button, Col, Badge } from 'antd';
import { Link } from 'react-router-dom';
import headerLogo from '../../images/ptwayLogoHeader.png';
import userLogo from '../../images/transparent-colored.png';
import header from '../../images/Page1_header.svg';
import { logout } from '../../store/actions/userAction';
import './style.scss';
import { withTranslation } from 'react-i18next';
import { loadState } from '../../_core/localStorage';
import { connect } from 'react-redux';
import { unreadJobOffers } from '../../store/actions/user/HomeActions';
import history from '../../_core/history';
import AddNewProjectModal from './AddNewProjectModal';
import AddNewAdModal from './AddNewAdModal';
import Navbar from './Navbar';
import LoginNavbar from './LoginNavbar';
import ptwayLogo from '../../images/PTway_Logo.svg';

import {
  addNewProject,
  allCotracts
} from '../../store/actions/company/projects';

class Header extends React.Component {
  state = {
    visible: false,
    notification: false,
    postJobPopup: false,
    newAdPopUp: false,
    addProject: false,
    userVisible: false,
    companyVisible: false
  };

  async componentDidMount() {
    const { unreadJobOffers, getContracts } = this.props;
    if (loadState().role === 'user') {
      unreadJobOffers();
    }
    if (loadState().role === 'user' || loadState().role === 'company') {
      getContracts();
    }
  }
  showDrawer = () => {
    this.setState({
      visible: true
    });
  };
  showCompanyDrawer = () => {
    this.setState({
      companyVisible: true
    });
  };
  showUserDrawer = () => {
    this.setState({
      userVisible: true
    });
  };
  onClose = () => {
    this.setState({
      visible: false,
      userVisible: false,
      companyVisible: false,
      postJobPopup: false,
      newAdPopUp: false
    });
  };

  notificationMenu = () => {
    this.setState({
      notification: !this.state.notification
    });
  };

  close = () => {
    this.setState({ notification: false });
  };

  postJob = () => {
    this.setState({
      addProject: false,
      postJobPopup: true
    });
  };

  newAd = () => {
    const { addProject } = this.props;
    const { projectName, projectDescription } = this.state;
    addProject({
      projectName,
      projectDescription
    });
    this.setState({
      postJobPopup: false,
      addProject: false,
      newAdPopUp: true
    });
  };

  render() {
    const { contracts } = this.props;
    // const { i18n } = this.props;
    const { role, loggedIn } = loadState();
    const list = [1, 2, 3, 4];
    const { unreadOffers } = this.props.userS;

    return (
      <React.Fragment>
        {loggedIn && role === 'user' ? (
          <React.Fragment>
            <Row className="user-header">
              <div className="user-right-side">
                <img
                  src={userLogo}
                  alt="logo"
                  style={{ width: '140px', cursor: 'pointer' }}
                  onClick={() => history.push('/user/home')}
                />
                <Link
                  to="/user/home"
                  className={
                    window.location.href.includes('/user/home')
                      ? 'navbar-elm-active'
                      : ''
                  }
                >
                   &nbsp;&nbsp;&nbsp;&nbsp;
                </Link>
                <Link
                  to="/user/jobs"
                  style={{ marginBottom: '0' }}
                  className={
                    window.location.href.includes('/user/jobs')
                      ? 'navbar-elm-active'
                      : ''
                  }
                >
                  <Badge
                    count={unreadOffers.count}
                    showZero
                    style={{ marginBottom: '10px', direction: 'ltr' }}
                  />
                   
                </Link>
              </div>
              <div className="user-left-side">
                <Button className="user-header-btn">
                  <Link
                    to="/user/account/setting"
                    className={
                      window.location.href.includes('/user/account/setting')
                        ? 'navbar-elm-active'
                        : ''
                    }
                  >
                    
                  </Link>
                </Button>
                {/* <Button
                  className="user-header-btn"
                  onClick={this.notificationMenu}
                >
                  
                </Button> */}
                {/* <div onBlur={this.close} tabIndex="0">
                  {this.state.notification && (
                    <div className="notifications-dropdown">
                      <h5></h5>
                      {list.map(elm => {
                        return (
                          <div className="notification-drop-menu" key={elm}>
                            <i
                              className="fa fa-picture-o"
                              aria-hidden="true"
                              style={{
                                fontSize: '45px',
                                display: 'flex',
                                alignItems: 'center',
                                paddingLeft: '10px'
                              }}
                            ></i>
                            <span>
                                       
                                
                            </span>
                          </div>
                        );
                      })}
                      <u className="more-notification-btn">
                        <Link
                          to="/user/notifications"
                          className="more-notification-btn"
                        >
                           
                        </Link>
                      </u>
                    </div>
                  )}
                </div>
                */}
                <Button
                  className="user-header-btn"
                  onClick={async () => {
                    await this.props.logout();
                    history.push('/');
                  }}
                >
                  
                </Button>
              </div>
            </Row>
            <Row className="drawer-mobile">
              <i
                className="fa fa-bars"
                aria-hidden="true"
                type="primary"
                onClick={this.showUserDrawer}
                style={{ color: '#059ad0' }}
              ></i>
              <img
                src={ptwayLogo}
                alt="logo"
                style={{ width: '30%' }}
                onClick={() => history.push('/')}
              />
              <Drawer
                title={
                  <div className="drawer-title-con">
                    <i
                      className="fa fa-times drawer-close"
                      aria-hidden="true"
                      onClick={this.onClose}
                    ></i>
                    <img src={userLogo} alt="logo" style={{ width: '45%' }} />
                  </div>
                }
                placement="right"
                closable={false}
                onClose={this.onClose}
                visible={this.state.userVisible}
              >
                <div className="navbar-user-mobile">
                  <Link to="/user/home">  </Link>
                  <Link to="/user/jobs" style={{ position: 'relative' }}>
                    <Badge
                      count={unreadOffers.count}
                      showZero
                      style={{ marginBottom: '10px', direction: 'ltr' }}
                    />
                     
                  </Link>

                  <Link to="/user/account/setting"></Link>
                  {/* <Link to="/user/notifications"></Link> */}
                  <div onBlur={this.close} tabIndex="0"></div>
                  <a
                    onClick={async () => {
                      await this.props.logout();
                      history.push('/');
                    }}
                  >
                     
                  </a>
                </div>
              </Drawer>
            </Row>
          </React.Fragment>
        ) : loggedIn && role === 'company' ? (
          <React.Fragment>
            <div
              className="user-header"
              style={{ justifyContent: 'space-around' }}
            >
              <div className="user-right-side">
                <img
                  src={userLogo}
                  alt="logo"
                  style={{ width: '140px' }}
                  onClick={() => history.push('/company/home')}
                />
                <Link
                  to="/company/home"
                  className={
                    window.location.href.includes('/company/home')
                      ? 'navbar-elm-active'
                      : ''
                  }
                >
                  &nbsp;&nbsp;
                </Link>
                <Link
                  to="/company/projects"
                  className={
                    window.location.href.includes('/company/projects')
                      ? 'navbar-elm-active'
                      : ''
                  }
                >
                  &nbsp;&nbsp;  
                </Link>
              </div>
              <div className="user-left-side">
                <Button
                  className="my-account-btn"
                  onClick={() =>
                    this.setState({ addProject: !this.state.addProject })
                  }
                >
                  <i className="fa fa-plus plus-icon" aria-hidden="true"></i>
                   
                </Button>
                {this.state.addProject && (
                  <div className="add-project-popup">
                    <div onClick={this.newAd}>
                      <i className="fa fa-plus-circle" aria-hidden="true"></i>
                         
                    </div>
                    <div onClick={this.postJob}>
                      <i
                        className="fa fa-plus-circle"
                        aria-hidden="true"
                        style={{ marginLeft: '7px' }}
                      ></i>
                         
                    </div>
                  </div>
                )}
                <Button
                  className="user-header-btn"
                  // onClick={() => this.props.history.push('/user/account/setting')}
                >
                  <Link
                    to="/company/setting"
                    className={
                      window.location.href.includes('/company/setting')
                        ? 'navbar-elm-active'
                        : ''
                    }
                  >
                    
                  </Link>
                </Button>
                {/* <Button
                  className="user-header-btn"
                  onClick={this.notificationMenu}
                >
                  
                </Button> */}
                <div onBlur={this.close} tabIndex="0">
                  {this.state.notification && (
                    <div className="notifications-dropdown">
                      <h5></h5>
                      {list.map(elm => {
                        return (
                          <div className="notification-drop-menu" key={elm}>
                            <i
                              className="fa fa-picture-o"
                              aria-hidden="true"
                              style={{
                                fontSize: '45px',
                                display: 'flex',
                                alignItems: 'center',
                                paddingLeft: '10px'
                              }}
                            ></i>
                            <span>
                                       
                                
                            </span>
                          </div>
                        );
                      })}
                      <u className="more-notification-btn">
                        <Link className="more-notification-btn">
                           
                        </Link>
                      </u>
                    </div>
                  )}
                </div>
                <Button
                  className="user-header-btn"
                  onClick={async () => {
                    await this.props.logout();
                    history.push('/');
                  }}
                >
                  
                </Button>
              </div>
              <Drawer
                title={
                  <img src={headerLogo} alt="logo" style={{ width: '90%' }} />
                }
                placement="right"
                closable={false}
                onClose={this.onClose}
                visible={this.state.visible}
              >
                <div className="navbar-mobile">
                  <Link to="/company/home"> </Link>
                  <Link to="/company/projects">  </Link>
                  {/* <Link to="/company/applicants"></Link> */}
                </div>
                <div className="user-left-side">
                  <Button
                    className="my-account-btn"
                    onClick={() =>
                      this.setState({ addProject: !this.state.addProject })
                    }
                  >
                    <i
                      className="fa fa-plus plus-icon"
                      aria-hidden="true"
                      style={{ marginLeft: '7px' }}
                    ></i>
                    
                  </Button>
                  {this.state.addProject && (
                    <div className="add-project-popup">
                      <div onClick={this.newAd}>
                        <i className="fa fa-plus-circle" aria-hidden="true"></i>
                           
                      </div>
                      <div onClick={this.postJob}>
                        <i
                          className="fa fa-plus-circle"
                          aria-hidden="true"
                          style={{ marginLeft: '7px' }}
                        ></i>
                           
                      </div>
                    </div>
                  )}
                  <Button
                    className="user-header-btn"
                    // onClick={() => this.props.history.push('/user/account/setting')}
                  >
                    <Link to="/company/setting"></Link>
                  </Button>
                  {/* <Button
                    className="user-header-btn"
                    onClick={this.notificationMenu}
                  >
                    
                  </Button> */}
                  <div onBlur={this.close} tabIndex="0">
                    {this.state.notification && (
                      <div className="notifications-dropdown">
                        <h5></h5>
                        {list.map(elm => {
                          return (
                            <div className="notification-drop-menu" key={elm}>
                              <i
                                className="fa fa-picture-o"
                                aria-hidden="true"
                                style={{
                                  fontSize: '45px',
                                  display: 'flex',
                                  alignItems: 'center',
                                  paddingLeft: '10px'
                                }}
                              ></i>
                              <span>
                                         
                                  
                              </span>
                            </div>
                          );
                        })}
                        <u className="more-notification-btn">
                          <Link className="more-notification-btn">
                             
                          </Link>
                        </u>
                      </div>
                    )}
                  </div>
                  <Button
                    className="user-header-btn"
                    onClick={async () => {
                      await this.props.logout();
                      history.push('/');
                    }}
                  >
                    
                  </Button>
                </div>
              </Drawer>
            </div>
            <Row className="drawer-mobile">
              <i
                className="fa fa-bars"
                aria-hidden="true"
                type="primary"
                onClick={this.showUserDrawer}
                style={{ color: '#059ad0' }}
              ></i>
              <img
                src={ptwayLogo}
                alt="logo"
                style={{ width: '30%' }}
                onClick={() => history.push('/')}
              />
              <Drawer
                title={
                  <div className="drawer-title-con">
                    <i
                      className="fa fa-times drawer-close"
                      aria-hidden="true"
                      onClick={this.onClose}
                    ></i>
                    <img src={userLogo} alt="logo" style={{ width: '45%' }} />
                  </div>
                }
                placement="right"
                closable={false}
                onClose={this.onClose}
                visible={this.state.userVisible}
              >
                <div className="navbar-user-mobile">
                  <Link to="/company/home"> </Link>
                  <Link to="/company/projects">  </Link>
                  <Link to="/company/setting"></Link>
                  {/* <Link to="/user/notifications"></Link> */}
                  <div onBlur={this.close} tabIndex="0"></div>
                  <a
                    onClick={async () => {
                      await this.props.logout();
                      history.push('/');
                    }}
                  >
                     
                  </a>
                </div>
              </Drawer>
            </Row>
          </React.Fragment>
        ) : window.location.href.includes('/signup') ||
          window.location.href.includes('/login') ? (
          <LoginNavbar />
        ) : (
          <React.Fragment>
            <Navbar />
            <div className="home-header">
              <Col md={12} sm={24}>
                <img src={header} alt="Page1_header" />
              </Col>
              <Col md={12} sm={24} className="brief">
                <h2 className="brief-title">
                       <br />    !
                </h2>
                <p className="brief-desc">
                         <br />
                          .
                </p>
                <button
                  className="log-btn"
                  onClick={() => history.push('/company/signup')}
                >
                    
                </button>
                <br />
                <br />
                <button
                  className="log-btn"
                  onClick={() => history.push('/user/signup')}
                >
                     
                </button>
              </Col>
            </div>
          </React.Fragment>
        )}
        <AddNewProjectModal
          postJobPopup={this.state.postJobPopup}
          newAd={this.newAd}
          onChange={e => {
            this.setState({ [e.target.name]: e.target.value });
          }}
          closable={this.onClose}
        />
        <AddNewAdModal
          newAdPopUp={this.state.newAdPopUp}
          contractsTypes={contracts}
          history={history}
          closable={this.onClose}
        />
      </React.Fragment>
    );
  }
}

const mapPropsToState = ({ user, userS, companyProjects }) => {
  return {
    user,
    userS,
    contracts: companyProjects
  };
};
const mapPropsToDispatch = dispatch => {
  return {
    logout: () => dispatch(logout()),
    unreadJobOffers: () => dispatch(unreadJobOffers()),
    addProject: params => dispatch(addNewProject(params)),
    getContracts: () => dispatch(allCotracts())
  };
};

const HeaderWrapper = connect(mapPropsToState, mapPropsToDispatch)(Header);

export default withTranslation()(HeaderWrapper);
