import React from 'react';
import './style.scss';
import { Form, Icon, Input, Button, Checkbox, Select, Alert } from 'antd';
import { Link } from 'react-router-dom';
import Footer from '../../Footer';
import statatisticsService from '../../../services/statisticsService';
import { companySignup } from '../../../store/actions/userAction';

import { connect } from 'react-redux';
import Header from '../../Header';
import SEO from '../../SEO';

const { getAllCompanyMajors, getCompanySMajor } = statatisticsService;
const { Option } = Select;

class CompanySignupForm extends React.Component {
  state = {
    sectors: [],
    jobTypes: []
  };
  componentDidMount = async () => {
    window.scrollTo(0, 0);
    const sectors = await getAllCompanyMajors();
    const jobTypes = await getCompanySMajor();
    this.setState({
      sectors,
      jobTypes
    });
  };
  handleSubmit = async e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        const { register, history } = this.props;
        await register({
          companyName: values.companyname,
          email: values.email,
          password: values.password,
          sector: values.sector,
          specialist: values.type,
          status: values.sector === '5c56c3572e168a2c30fe5dde' ? false : true
        });

        history.push('/company/profile');
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { jobTypes, sectors } = this.state;
    const { user } = this.props;
    return (
      <React.Fragment>
        <SEO
          title=""
          description="  !       "
        />
        <Header />
        <div className="company-signup-container">
          <div className="form-container">
            <h3 className="login-form-title">  </h3>
            <Form onSubmit={this.handleSubmit} style={{ width: '100%' }}>
              <label className="login-form-label"> </label>
              <Form.Item>
                {getFieldDecorator('companyname', {
                  rules: [{ required: true, message: '   ' }]
                })(<Input prefix={<Icon />} />)}
              </Form.Item>
              <label className="login-form-label"> </label>
              <Form.Item>
                {getFieldDecorator('email', {
                  rules: [
                    {
                      required: true,
                      message: '   '
                    }
                  ]
                })(<Input prefix={<Icon />} />)}
              </Form.Item>
              <label className="login-form-label"> </label>
              <Form.Item>
                {getFieldDecorator('type', {
                  rules: [
                    { required: true, message: '   ' }
                  ]
                })(
                  <Select className="type-selector">
                    {jobTypes.map(elm => {
                      return (
                        <Option value={elm.id} key={elm.id}>
                          {elm.value}
                        </Option>
                      );
                    })}
                  </Select>
                )}
              </Form.Item>
              <label className="login-form-label"></label>
              <Form.Item>
                {getFieldDecorator('sector', {
                  rules: [
                    { required: true, message: '   ' }
                  ]
                })(
                  <Select className="sector-selector">
                    {sectors.map(elm => {
                      return (
                        <Option value={elm.id} key={elm.id}>
                          {elm.value}
                        </Option>
                      );
                    })}
                  </Select>
                )}
              </Form.Item>
              <label className="login-form-label"> </label>
              <Form.Item>
                {getFieldDecorator('password', {
                  rules: [
                    { required: true, message: '   ' }
                  ]
                })(<Input prefix={<Icon />} type="password" />)}
              </Form.Item>
              {/* <Form.Item>
                {getFieldDecorator('remember', {
                  valuePropName: 'checked',
                  initialValue: true
                })(<Checkbox>  </Checkbox>)}
              </Form.Item> */}
              {user.signupError && user.signupError.response ? (
                <Alert
                  message={user.signupError.response.data}
                  type="error"
                  style={{ width: '100%' }}
                />
              ) : (
                ''
              )}
              <br />
              <div className="login-btn-cont">
                <Button
                  type="primary"
                  htmlType="submit"
                  className="login-form-button login-form-btn"
                >
                   
                </Button>
              </div>
              <div className="create-new-account">
                  <Link to="/company/login"> </Link>
              </div>
            </Form>
          </div>
          <div style={{ width: '100%' }}>
            <Footer />
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    user: state.user
  };
};
const mapDispatchToProps = dispatch => {
  return {
    register: params => {
      return dispatch(companySignup(params));
    }
  };
};

const CompanyRegistration = Form.create({ name: 'CompanySignupForm' })(
  CompanySignupForm
);
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CompanyRegistration);
